hurch")]),_vm._v(" What implications does this passage have for you as a church member, for your fellow church family, for the church in your city/region?\n      What would it look like for you to be a living example of this text in your church? To what degree are you this example now?\n      To what degree is your church an example of this passage now? What would it look like for the members of the church you belong to to be a living example of this text?\n      How would it affect your city if churches there were living examples of this text? To what degree are they this example now?\n      Pray. Concerning numerals II and III above, (a) thank God for any ways the church is a godly example. (b) Ask God for His power of regeneration in the areas the church is not the example it should be. (c) Ask God to show you how He would have you contribute to the continuance of a godly example and (d) to the solution for any deficient example.\n    ")]),_vm._v(" "),_c('div',{staticClass:"activity theme-mid small-pad muted-more-bottom"},[_c('div',{staticClass:"activity-title"},[_vm._v("Stewardship")]),_vm._v(" What implications does this passage have on how you steward your time, money, thoughts and relationships?\n      Set the context for this activity by answering the following questions: In the next 3 days…\n      … What will you spend time doing?\n      … For what will you spend money?\n      … What attitudes and ideas will you think about?\n      … Who will you see and interact with?\n      Now read the passage again. Finally, answer: How should this passage affect...\n      … the way you spend your time?\n      … the way you spend your money?\n      … the things you feel and think?\n      … the way you interact with people?\n    ")]),_vm._v(" "),_c('div',{staticClass:"activity theme-mid small-pad muted-more-bottom"},[_c('div',{staticClass:"activity-title"},[_vm._v("SPACE")]),_vm._v(" As you read the passage, what does Spirit reveal in the following areas:\n      Sin I need to confess\n      Promises I can claim\n      Action I need to take\n      Commands I must obey\n      Examples I can follow\n    ")]),_vm._v(" "),_c('div',{staticClass:"activity theme-mid small-pad muted-more-bottom"},[_c('div',{staticClass:"activity-title"},[_vm._v("ACTS")]),_vm._v(" This is an acronym and is primarily an activity of prayer. Consider the primer questions for each word in the acronym, then direct your thoughts toward God in prayer:\n      "),_c('div',{staticClass:"activity-title"},[_vm._v("Adoration")]),_vm._v(" what does this passage teach about God’s character or attributes? Praise God for these qualities.\n      "),_c('div',{staticClass:"activity-title"},[_vm._v("Confession")]),_vm._v(" what specific or general sinfulness does Spirit reveal in your life through this passage? Confess this to God, accept His forgiveness and power to change.\n      "),_c('div',{staticClass:"activity-title"},[_vm._v("Thankfulness")]),_vm._v(" how does this passage reveal things you can be thankful for? Thank God for these things.\n      "),_c('div',{staticClass:"activity-title"},[_vm._v("Supplication")]),_vm._v(" how are you or someone you know in need? Ask God to supply His deliverance in these things.\n    ")]),_vm._v(" "),_c('div',{staticClass:"activity theme-mid small-pad muted-more-bottom"},[_c('div',{staticClass:"activity-title"},[_vm._v("Outward-Inward")]),_vm._v(" This activity is meant to help you think honestly about differences in who you are to others, in your habits, in your values and in your inner self.\n      Observe the different character qualities in the text. For each question below, you are being measured in 2 ways: (1) to what degree you are an example of each character quality, and (2) to what degree you are relying on yourself versus relying on Spirit for this character quality.\n      If your family were to compare you to these qualities, how would they measure you? Your co-workers, neighbors, etc.?\n      How would you measure your actual habits and behaviors compared to these qualities?\n      How would you measure your values and beliefs compared to these qualities?\n      How would you measure your actual thoughts and desires compared to these qualities?\n    ")]),_vm._v(" "),_c('div',{staticClass:"activity theme-mid small-pad muted-more-bottom"},[_c('div',{staticClass:"activity-title"},[_vm._v("I am & I am not")]),_vm._v(" What is happening in this text? What is commanded or modeled? How are you doing these things? How are you not doing these things? What is Jesus making you in regards to these things?\n      When thinking about this passage, and being honest with myself:\n      I am…  (being unfruitful by…)\n      I am not… (being unfruitful by…)\n      Jesus is doing this in my life… (helping me to be fruitful by…)\n    ")]),_vm._v(" "),_c('div',{staticClass:"activity theme-mid small-pad muted-more-bottom"},[_c('div',{staticClass:"activity-title"},[_vm._v("Will, Intellect & Fantasy")]),_vm._v(" To what degree have the truths in this passage settled in to who you are? Are you living in obedience to these truths? Are you only intellectually agreeing with these truths but not obeying? Or are you aware of these teachings but not personally agreeing to them?\n    ")])])])}]
